.calendar {
  margin: 0;
  padding: 30px;
  background-color: rgba(255, 255, 255, 1);
  display: flex;
  align-items: flex-start;
  justify-content: space-around;
  flex-direction: column;
  color: black;
  border-radius: 30px;
  position: relative;
  box-sizing: border-box;
}

/* ABOUT */
.calendar__about {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  width: 100%;
}

.calendar__participants {
  width: 50%;
  margin: 0 0 10px 0;
  padding: 0;
  color: #464646;
  font-family: 'PT Root', sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  text-align: left;
  align-self: flex-start;
}

.calendar__date {
  margin: 0 0 10px 0;
  padding: 0;
  font-family: 'PT Root', sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  text-align: right;
}

.calendar__event {
  max-width: 320px;
  margin: 0;
  padding: 0;
  font-family: 'Proto', sans-serif;
  font-weight: 300;
  font-size: 23px;
  line-height: 30px;
  text-align: left;
}

.calendar__day {
  margin: 0;
  padding: 0;
  font-family: 'Proto', sans-serif;
  font-weight: 300;
  font-size: 100px;
  line-height: 100px;
  letter-spacing: -2px;
  text-align: right;
}

/* CONTACTS */
.calendar__contacts {
  min-width: 100%;
  margin: 50px 0 30px 0;
  padding: 0;
  font-family: 'PT Root', sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  list-style-type: none;
}

.calendar__contacts_place_main {
  margin-top: 156px;
}

.calendar__contacts-item {
  margin: 0;
  padding: 0;
  color: #1A1A1A;
  border-bottom: 1px solid rgba(167, 166, 166, 1);
}

.calendar__contacts-item:last-child {
  margin-bottom: 0;
  border: none;
}

.calendar__time {
  max-width: 100%;
  margin: 0 0 10px 0;
  padding: 0;
  text-align: left;
}

.calendar__adress {
  max-width: 100%;
  margin: 10px 0;
  padding: 0;
  text-align: left;
}

.calendar__phone {
  margin: 10px 0 0 0;
  padding: 0;
  text-align: left;
  color: black;
}


/* BUTTONS */
.calendar__sign-up {
  min-width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.calendar__sign-up__type_text {
  margin: 0 0 0 15px;
  padding: 0;
  font-family: 'PT Root', sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
}

.calendar__sign-up_flex {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.calendar__btn {
  width: 48px;
  height: 48px;
}

/*CALENDAR ONCLICK*/
.calendar_onclick {
  background-color: #C8D1FF;
}


/*POPUP*/
.calendar__popup {
  position: relative;
  max-width: 770px;
  padding: 50px 100px;
  box-sizing: border-box;
}

.calendar__about_popup {
  width: 100%;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.calendar__text_popup {
  margin: 0 0 30px 0;
  padding: 0;
  font-family: 'PT Root', sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 26px;
  color: rgba(26, 26, 26, 1);
}


/*--------MEDIA--------*/

@media (max-width: 1620px) {

  .calendar__event {
    max-width: 320px;
  }

  .calendar__contacts_place_main {
    margin-top: 50px;
  }
}


@media (max-width: 1250px) {

  .calendar {
    padding-left: 15px;
    padding-right: 15px;
  }

  .calendar__popup {
    padding: 50px 100px;
  }


  .calendar__participants {
    font-size: 12px;
    line-height: 17px;
  }

  .calendar__date {
    font-size: 12px;
    line-height: 17px;
  }

  .calendar__event {
    max-width: 246px;
    font-size: 19px;
    line-height: 23px;
  }

  .calendar__day {
    font-size: 60px;
    line-height: 60px;
  }

  .calendar__contacts {
    font-size: 12px;
    line-height: 17px;
  }

  .calendar__sign-up__type_text {
    font-size: 12px;
    line-height: 17px;
  }

  .calendar__sign-up__type_text {
    margin-left: 10px;
  }

  .calendar__time {
    margin-bottom: 5px;
  }

  .calendar__adress {
    margin-bottom: 5px;
    margin-top: 5px;
  }

  .calendar__phone {
    margin-top: 5px;
  }

  .calendar__btn {
    width: 40px;
    height: 40px;
  }
}

@media (max-width: 440px) {

  .calendar__event {
    max-width: 166px;
  }
}




/*-------------POPUP---------------*/


@media (max-width: 640px) {

  .calendar__popup {
    padding: 60px 15px 30px;
  }

  .calendar__text_popup {
    font-size: 12px;
    line-height: 17px;
  }
}
