.logo {
  text-decoration: none;
  display: flex;
}

.logo__img {
  width: 100%;
  height: 100%;
}

.logo_place_footer {
  width: 415px;
  height: 76px;
  grid-area: logo;
}

.logo_place_mainpage {
  width: 252px;
  height: 46px;
  margin: 40px 0 70px 30px;
}

.logo_place_about {
  width: 357px;
  height: 65px;
  margin: 50px auto 80px;
}

@media (max-width: 1440px) {
  .logo_place_mainpage {
      margin-top: 35px;
      margin-bottom: 43px;
  }
}

@media (max-width: 1230px) {
  .logo_place_mainpage {
      width: 164px;
      height: 30px;
      margin: 27px 0 33px 15px;
  }

  .logo_place_footer {
      width: 252px;
      height: 46px;
  }

  .logo_place_about {
      width: 252px;
      height: 46px;
      margin: 30px auto 50px;
  }
}

@media (max-width: 860px) {
  .logo_place_mainpage {
      margin-top: 22px;
      margin-bottom: 26px;
  }

  .logo_place_about {
      margin: 20px auto 30px;
  }
}
