.mainvideo {
  display: flex;
  border-radius: 30px;
  width: 100%;
}

.mainvideo__description {
  background: #F8D162;
  padding: 30px 50px;
  border-radius: 30px;
  width: 32.26%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  position: relative;
  box-sizing: border-box;
}

.mainvideo__name {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.mainvideo__title {
  font-family: 'Proto', Arial, sans-serif;
  font-weight: 300;
  font-size: 23px;
  line-height: 30px;
  text-align: center;
  margin: 0 0 5px 0;
}

.mainvideo__caption {
  font-family: 'PT Proto', Arial, sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.2px;
  color: #464646;
  margin: 0;
}

.mainvideo__link {
  font-family: 'Proto', Arial, sans-serif;
  font-weight: 300;
  font-size: 15px;
  line-height: 19px;
  letter-spacing: 0.2px;
  color: #224CFF;
  margin: 0;
  text-decoration: none;
  z-index: 4;
}

.mainvideo__link:hover {
  color: #2E2468;
}

.mainvideo__video {
  border-radius: 30px;
  width: 67.74%;
  height: auto;
  cursor: pointer;
}

@media (max-width: 1440px) {
  .mainvideo__video {
    width: 68.376%;
  }

  .mainvideo__description {
    padding-left: 30px;
    padding-right: 30px;
    width: 31.624%;
  }

}

@media (max-width: 1024px) {
  .mainvideo {
    flex-direction: column;
  }

  .mainvideo__description {
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    padding: 15px;
    width: 100%;
    order: 2;
  }

  .mainvideo__video {
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    width: 100%;
    order: 1;
  }

  .mainvideo__name {
    margin-bottom: 55px;
  }


  .mainvideo__title {
    font-size: 19px;
    line-height: 23px;
  }

  .mainvideo__caption {
    font-size: 12px;
    line-height: 17px;
  }

  .mainvideo__link {
    font-size: 12px;
    line-height: 17px;
  }
}

@media (max-width: 640px) {
  .mainvideo__name {
    margin-bottom: 32px;
  }
}
