.personal-account {
  margin: 30px auto 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1;
  overflow: visible;
}

.personal-account__buttons {
  margin: 0;
  margin-bottom: 30px;
  padding: 0;
  display: flex;
  flex-direction: row;
  align-self: flex-end;
}

.personal-account__button_closed {
  display: none;
}

.personal-account__text {
  margin: 0;
  margin-right: 30px;
  padding: 0;
  font-family: 'PT Root', sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 26px;
  text-align: right;
  color: #a7a6a6;
  cursor: pointer;
}

.personal-account__photo-text_closed {
  display: none;
}

.personal-account__text:hover {
  color: #224cff;
}

.personal-account__text:last-of-type {
  margin-right: 0;
}

.personal-account__events {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-bottom: 50px;
  margin-bottom: 50px;
  border-bottom: 1px solid #464646;
}

.personal-account__title {
  margin: 0;
  padding: 0;
  font-family: 'Proto', sans-serif;
  font-weight: 300;
  font-size: 23px;
  line-height: 30px;
  text-align: center;
  color: #1a1a1a;
}

.personal-account__title_type_content {
  width: 100%;
  margin-bottom: 50px;
}

/*---------------------------*/
.personal-account__form {
  width: 100%;
  height: 472px;
  margin: 0;
  margin-bottom: 80px;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(1, 40% 60%);
}

.personal-account__photo {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: white;
  border-radius: 30px;
  overflow: hidden;
}

.personal-account__button {
  width: 100px;
  height: 100px;
  margin: 0;
  margin-bottom: 15px;
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  font-size: 0;
  line-height: 0;
  opacity: 0;
  background-image: url('../../assets/img/personal-account-btn-add-photo.svg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.personal-account__photo-text {
  margin: 0;
  padding: 0;
  font-family: 'Proto', sans-serif;
  font-weight: 300;
  font-size: 15px;
  line-height: 19px;
  color: #a7a6a6;
  cursor: pointer;
  position: relative;
}

.personal-account__photo-text::before {
  content: '';
  display: block;
  position: absolute;
  bottom: 33px;
  left: 7px;
  width: 100px;
  height: 100px;
  background-image: url('../../assets/img/personal-account-btn-add-photo.svg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.personal-account__photo-title {
  display: block;
}

.personal-account__inputs {
  margin: 0;
  padding: 30px;
  background-color: white;
  border-radius: 30px;
  display: grid;
  grid-template-columns: auto 30px auto;
  grid-template-rows: 1fr 286px;
  font-size: 0;
  line-height: 0;
}

.personal-account__input {
  margin-bottom: 20px;
  padding: 13px 20px 16px 20px;
  background-color: #f3f2f2;
  border: none;
  border-radius: 30px;
  color: #000;
  font-family: 'Proto', sans-serif;
  font-weight: 300;
  font-size: 15px;
  line-height: 19px;
  text-align: left;
}
.personal-account__input-error::placeholder {
  color: #ff4e4e;
}

.personal-account__input_type_place {
  grid-column: 1/2;
}

.personal-account__input_type_date {
  /* width: 30.5%; */
  /* margin-left: 4%; */
  grid-column: 3/4;
}

.personal-account__textarea {
  display: block;
  /* width: 93%;
  height: 286px; */
  margin: 0;
  padding: 13px 20px 16px 20px;
  background-color: #f3f2f2;
  border: none;
  border-radius: 30px;
  color: #000;
  font-family: 'Proto', sans-serif;
  font-weight: 300;
  font-size: 15px;
  line-height: 19px;
  text-align: left;
  grid-column: 1/4;
}

/*---------------------------*/
.personal-account__feedback {
  margin: 0;
  margin-top: 30px;
  padding: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  grid-column: 1/2;
}

.personal-account__radio {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.personal-account__feedback-button {
  width: 26px;
  height: 26px;
  margin: 0;
  margin-right: 15px;
  padding: 0;
  border: none;
  background-color: transparent;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}

.personal-account__feedback-button_closed {
  display: none;
}

.personal-account__feedback-button_good {
  background-image: url('../../assets/img/feedback-good-black.svg');
}

.personal-account__feedback-button_normal {
  background-image: url('../../assets/img/feedback-normal-black.svg');
}

.personal-account__feedback-button_bad {
  background-image: url('../../assets/img/feedback-bad-black.svg');
}

.personal-account__feedback-button_good-active,
.personal-account__feedback-button_good + .personal-account__radio:checked {
  /* .personal-account__feedback-button_good[for="good"] { */
  background-image: url('../../assets/img/feedback-good-color.svg');
}

.personal-account__feedback-button_normal-active,
.personal-account__radio:checked + .personal-account__feedback-button_normal {
  background-image: url('../../assets/img/feedback-normal-color.svg');
}

.personal-account__feedback-button_bad-active,
.personal-account__radio:checked + .personal-account__feedback-button_bad {
  background-image: url('../../assets/img/feedback-bad-color.svg');
}

.personal-account__label {
  cursor: pointer;
}

.personal-account__feedback-text {
  margin: 0;
  padding: 0;
  font-family: 'PT Root', sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  color: #a7a6a6;
  text-align: left;
}

.personal-account__feedback-text_type_good {
  color: #8cdd94;
}

.personal-account__feedback-text_type_normal {
  color: #464646;
}

.personal-account__feedback-text_type_bad {
  color: #ff8585;
}

.personal-account__feedback-btn {
  margin: 0;
  margin-right: 20px;
  padding: 0;
  font-family: 'Proto', sans-serif;
  font-weight: 300;
  font-size: 15px;
  line-height: 19px;
  color: #a7a6a6;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.personal-account__feedback-btn_type_active {
  color: #000000;
}

.personal-account__feedback-btn:hover {
  color: #000000;
}

.personal-account__share::before {
  content: url('../../assets/img/arrow1.svg');
  width: 10px;
  height: 5px;
  position: absolute;
  top: 0;
  left: 152px;
  display: none;
}

.personal-account__submit {
  /* max-width: 27%; */
  margin: 0;
  margin-top: 30px;
  padding: 0;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  grid-column: 3/4;
}

/*------------PERSONAL-ACCOUNT-ADDED PROFILE----------------*/

.personal-account__event {
  margin: 30px 0 0;
  padding: 0;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  align-self: flex-start;
}

.personal-account__event-wrapper {
  margin: 0;
  margin-right: 30px;
  padding: 30px;
  background-color: white;
  border-radius: 30px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  overflow: hidden;
  min-width: 205px;
  max-width: 270px;
  box-sizing: border-box;
}
/* overflow f js */

.personal-account__event-wrapper:last-of-type {
  margin-right: 0;
}

.personal-account__inputs_added {
  grid-template-columns: 30% 30px auto;
}

.personal-account__subtitle-date {
  margin: 0;
  padding: 0;
  font-family: 'Proto', sans-serif;
  font-weight: 300;
  font-size: 44px;
  line-height: 50px;
  text-align: left;
}

.personal-account__subtitle-month {
  margin: 0;
  margin-bottom: 30px;
  margin-left: 10px;
  padding: 0;
  font-family: 'PT Root', sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  text-align: right;
  overflow: hidden;
}
/* overflow f js */

.personal-account__subtitle-event {
  margin: 0;
  margin-top: 10px;
  padding: 0;
  font-family: 'PT Root', sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  text-align: left;
}

.personal-account__event-text {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.personal-account__add-meet {
  width: 100%;
  margin: 0;
  margin-bottom: 50px;
  padding: 0;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  align-self: flex-start;
}

.personal-account__meet-text {
  margin: 0;
  margin-left: 15px;
  font-family: 'Proto', sans-serif;
  font-size: 15px;
  line-height: 19px;
  color: #224cff;
  border: none;
}

.personal-account__photo-opened {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  object-position: center;
  object-fit: cover;
}

.personal-account__input-heading {
  margin: 0;
  padding: 0;
  font-family: 'Proto', sans-serif;
  font-weight: 300;
  font-size: 23px;
  line-height: 20px;
  color: black;
  text-align: left;
}

.personal-account__event-head {
  display: flex;
  flex-direction: column-reverse;
  align-items: flex-end;
  grid-column: 3/4;
}

.personal-account__subtitle-month_place_form {
  margin-bottom: 10px;
}

.personal-account__textarea-text {
  width: 85%;
  height: 286px;
  overflow-y: scroll;
  overflow-x: hidden;
  scrollbar-color: #e8e6e6 transparent;
  scrollbar-width: thin;
  grid-column: 1/4;
  margin: 0;
  padding: 0;
  font-family: 'PT Root', sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 26px;
  color: black;
  text-align: left;
  padding-right: 10px;
}

.personal-account__textarea-text::-webkit-scrollbar {
  width: 3px;
  background-color: cover;
}

.personal-account__textarea-text::-webkit-scrollbar-thumb {
  background-color: #e8e6e6;
  border-radius: 5px;
}

.personal-account__textarea-text::-webkit-scrollbar-button {
  height: 15px;
}

/*---------------CLOSED----------------*/
.personal-account_closed {
  display: none;
}

/*---------------------MEDIA------------------------*/

@media (max-width: 1440px) {
  .personal-account__title_type_content {
    max-width: 770px;
  }
}

@media (max-width: 1150px) {
  .personal-account__form {
    height: 100%;
    grid-template-columns: 70%;
    grid-template-rows: 400px 600px;
    justify-content: center;
    margin-bottom: 50px;
  }

  .personal-account__inputs {
    display: flex;
    flex-direction: column;
  }

  .personal-account__textarea {
    height: 286px;
  }

  .personal-account__submit {
    max-width: 100%;
    flex-direction: row-reverse;
    justify-content: space-between;
  }

  .personal-account__event-head {
    margin-top: -20px;
  }

  .personal-account__submit-added {
    display: grid;
    margin-top: 20px;
    row-gap: 10px;
    grid-template-columns: 70% 30%;
  }

  .personal-account__feedback-btn:nth-of-type(1) {
    grid-column: 1/2;
    margin-right: 0;
    justify-self: start;
    position: relative;
  }

  .personal-account__feedback-btn:nth-of-type(2) {
    grid-column: 2/3;
    justify-self: end;
  }

  .personal-account__feedback-btn:nth-of-type(3) {
    grid-column: 2/3;
    grid-row: 2/3;
    justify-self: right;
  }
}

@media (max-width: 1024px) {
  .personal-account__title {
    font-size: 19px;
    line-height: 23px;
  }

  .personal-account__event {
    margin-top: 20px;
  }

  .personal-account__text {
    display: none;
  }

  .personal-account__event-wrapper {
    padding: 15px;
  }

  .personal-account__subtitle-date {
    font-size: 37px;
    line-height: 39px;
  }

  .personal-account__subtitle-month {
    font-size: 12px;
    line-height: 17px;
    margin-bottom: 22px;
  }

  .personal-account__subtitle-event {
    font-size: 12px;
    line-height: 17px;
    margin-top: 5px;
  }

  .personal-account__events {
    padding-bottom: 20px;
    margin-bottom: 20px;
  }
}

@media (max-width: 860px) {
  .personal-account__title_type_content {
    margin-bottom: 20px;
  }

  .personal-account__button {
    width: 80px;
    height: 80px;
  }

  .personal-account__photo-text::before {
    left: 16px;
    width: 80px;
    height: 80px;
  }

  .personal-account__feedback-text {
    font-size: 12px;
    line-height: 17px;
  }

  .personal-account__feedback-btn {
    font-size: 12px;
    line-height: 17px;
  }

  .personal-account__subtitle-month_place_form {
    font-size: 12px;
    line-height: 17px;
  }

  .personal-account__textarea-text {
    width: 100%;
    margin-top: 15px;
    font-size: 15px;
    line-height: 21px;
    padding-right: 7px;
  }

  .personal-account__input-heading {
    font-size: 19px;
    line-height: 23px;
  }

  .personal-account__feedback-button {
    width: 23px;
    height: 23px;
  }

  .personal-account__share::before {
    display: block;
  }
}

@media (max-width: 490px) {
  .personal-account {
    margin-top: 10px;
  }

  .personal-account__form {
    grid-template-columns: 290px;
    grid-template-rows: 291px 404px;
    justify-content: center;
    margin-bottom: 50px;
  }

  .personal-account__textarea {
    height: 120px;
    padding: 10px 20px 10px 20px;
    font-size: 12px;
    line-height: 17px;
  }

  .personal-account__inputs {
    padding-left: 15px;
    padding-right: 15px;
  }

  .personal-account__input {
    margin-bottom: 10px;
  }

  .personal-account__feedback {
    margin-top: 20px;
  }

  .personal-account__feedback-button {
    margin-right: 10px;
  }

  .personal-account__photo-text {
    font-size: 12px;
    line-height: 17px;
  }

  .personal-account__input {
    font-size: 12px;
    line-height: 17px;
  }

  .personal-account__feedback-btn:nth-of-type(2) {
    margin-top: 10px;
  }

  .personal-account__feedback-btn:nth-of-type(3) {
    margin-top: 10px;
  }
}
