.footer {
  width: 100%;
  background-color: #2E2468;
  margin: 0;
  padding: 0;
  flex-shrink: 0;
}

.footer__wrapper {
  max-width: 1200px;
  padding: 90px 0 80px 0;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(3, max-content);
  grid-template-areas: "logo btn nav"
                         "dev  dev nav";
  gap: 131px 85px;
  align-items: center;
}

.footer__help-text {
  margin: 0;
  padding: 0;
}

.button_place_footer:hover {
  color: #C8D1FF;
  border-color: #C8D1FF;
}

.footer__money {
  grid-area: btn;
  margin-right: 46px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 15px 20px;
  border: 1px solid #F3F2F2;
  color: #F3F2F2;
  border-radius: 100px;
  font-family: 'Proto', Arial, sans-serif;
  font-weight: 300;
  font-size: 15px;
  line-height: 19px;
  letter-spacing: 0.2px;
  text-decoration: none;
}

.footer__money:hover {
  color: #C8D1FF;
  border-color: #C8D1FF;
}

.footer__navigation {
  display: flex;
  grid-area: nav;
}

.footer__menu {
  margin-right: 44px;
}

.footer__menu:last-child {
  margin-right: 0;
}

.footer__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
}

.footer__list-item {
  margin: 0 0 20px 0;
  padding: 0;
  list-style-type: none;
}

.footer__list-item:last-child {
  margin-bottom: 0;
}

.footer__list-link {
  margin: 0;
  text-decoration: none;
  text-transform: uppercase;
  color: #F3F2F2;
  font-family: 'Proto', Arial, sans-serif;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 0.2px;
  font-weight: 300;
  text-align: left;
}

.footer__list-link:hover {
  color: #C8D1FF;
}

.footer__about {
  display: flex;
  flex-direction: column;
  grid-area: dev;
}

.footer__copyright {
  grid-area: copy;
  margin: 0 0 12px 0;
  padding: 0;
  font-family: 'PT Proto', Arial, sans-serif;
  font-size: 18px;
  line-height: 26px;
  font-weight: 400;
  color: #C8D1FF;
}

.footer__develop {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
}


.footer__develop-text {
  margin: 0 30px 0 0;
  padding: 0;
  font-family: 'PT Proto', Arial, sans-serif;
  font-size: 18px;
  line-height: 26px;
  font-weight: 400;
  color: #F3F2F2;
}

.footer__develop-text:last-child {
  margin-right: 0;
}

.footer__develop-accent {
  margin: 0;
  padding: 0;
  color: #F3F2F2;
}

.footer__develop-accent:hover {
  color: #C8D1FF;
}

@media (max-width: 1440px) {
  .footer__wrapper {
    width: calc(100% - 270px);
  }
}

@media (max-width: 1360px) {
  .footer__wrapper {
    width: calc(100% - 270px);
    margin-top: 40px;
    margin-bottom: 30px;
    grid-template-columns: 1fr;
    grid-template-areas: "logo"
                             "nav"
                             "btn"
                             "dev";
    gap: 31px;
    justify-items: center;
    justify-content: center;
  }

  .footer__menu {
    margin-right: 0;
  }

  .footer__money {
    margin-right: 0;
    padding: 11px 15px;
    font-size: 12px;
    line-height: 17px;
  }

  .footer__navigation {
    flex-direction: column;
    align-items: center;
  }

  .footer__list {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }

  .footer__list_type_nav {
    padding-bottom: 21px;
    border-bottom: 1px solid #C8D1FF;
  }

  .footer__list_type_social {
    padding-top: 21px;
  }

  .footer__list-item {
    margin-bottom: 0;
    margin-right: 15px;
  }

  .footer__list-item:last-child {
    margin-right: 0;
  }

  .footer__about {
    flex-direction: row;
  }

  .footer__copyright {
    margin-right: 30px;
    margin-bottom: 0;
    font-size: 12px;
    line-height: 17px;
  }

  .footer__develop-text {
    font-size: 12px;
    line-height: 17px;
  }

}

@media (max-width: 1024px) {
  .footer__wrapper {
    width: calc(100% - 260px);
  }

  .header__action {
    min-width: 30px;
  }
}

@media (max-width: 940px) {
  .footer__about {
    flex-direction: column;
  }

  .footer__copyright {
    margin-bottom: 5px;
    margin-right: 0;
    text-align: center;
  }

  .footer__develop {
    flex-direction: column;
  }

  .footer__develop-text {
    margin-bottom: 5px;
    margin-right: 0;
  }

  .footer__develop-text:last-child {
    margin-bottom: 0;
  }
}

@media (max-width: 640px) {
  .footer__wrapper {
    width: calc(100% - 32px);
  }

  .footer__list_type_nav {
    margin-top: 10px;
  }

  .footer__list-item {
    margin-bottom: 17px;
  }

  .footer__list-item:nth-last-child(2) {
    margin-bottom: 0;
  }
}
