.grid-calendar {
  margin: 80px auto 120px;
}

.grid-calendar__title {
  margin: 0 auto 40px auto;
  padding: 0;
  font-weight: 300;
  font-size: 74px;
  line-height: 80px;
  text-align: center;
}

.grid-calendar__buttons {
  width: 100%;
  height: 100%;
  margin: 0 auto 80px auto;
  padding: 0;
  display: flex;
  justify-content: center;
  text-align: center;
}

.grid-calendar__grid {
  width: 100%;
  margin: 0 auto;
  padding: 0;
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(3, 570px);
  column-gap: 30px;
  row-gap: 30px;
}

@media (max-width: 1900px) {

  .grid-calendar__grid {
    grid-template-columns: repeat(2, 570px);
  }
}

@media (max-width: 1439px) {

  .grid-calendar {
    width: 100%;
  }
}


@media (max-width: 1024px) {

  .grid-calendar {
    margin-top: 45px;
    margin-bottom: 100px;
  }

  .grid-calendar__title {
    font-size: 37px;
    line-height: 39px;
  }

  .grid-calendar__buttons {
    margin-bottom: 50px;
  }
}
@media (max-width: 1250px) {

  .grid-calendar__grid {
    grid-template-columns: repeat(2, 370px);
  }
}

@media (max-width: 850px) {
  .grid-calendar {
    width: calc(100% - 30px);
    margin-top: 25px;
    margin-bottom: 50px;
  }

  .grid-calendar__title {
    margin-bottom: 20px;
  }

  .grid-calendar__buttons {
    margin-bottom: 30px;
  }

  .grid-calendar__grid {
    grid-template-columns: 470px;
    row-gap: 30px;
    column-gap: 0;
  }
}


@media (max-width: 530px) {
  .grid-calendar__grid {
    grid-template-columns: 1fr;
  }
}
