.login {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  padding: 50px 100px;
  background-color: #fff;
  border-radius: 30px;
  max-width: 747px;
}

.login__title {
  font-family: 'Proto', Arial, sans-serif;
  font-weight: 300;
  font-size: 23px;
  line-height: 30px;
  color: #000;
  margin: 0 0 16px 0;
}

.login__texts {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 50px;
  max-width: 547px;
}

.login__text {
  font-family: 'PT Proto', Arial, sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 26px;
  color: #000;
  margin: 0 0 27px 0;
  text-align: center;
}

.login__text:last-child {
  margin-bottom: 0;
}

.login__link {
  font-family: 'PT Proto', Arial, sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  letter-spacing: 0.2px;
  color: #464646;
  margin: 0 0 30px 0;
  text-decoration: none;
}

.login__link:hover {
  color: #224cff;
}

@media screen and (max-width: 640px) {
  .login {
    padding: 60px 15px 50px;
  }

  .login__title {
    font-size: 19px;
    line-height: 23px;
    color: #000;
  }

  .login__texts {
    margin-bottom: 32px;
  }

  .login__text {
    font-weight: 400;
    font-size: 15px;
    line-height: 21px;
    margin-bottom: 20px;
  }

  .login__link {
    font-size: 12px;
    line-height: 17px;
  }
}
