.page-not-found {
  max-width: 770px;
  margin: 80px auto;
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.page-not-found__img {
  max-width: 570px;
  margin: 0;
  margin-bottom: 36px;
  padding: 0;
}

.page-not-found__title {
  margin: 0;
  margin-bottom: 30px;
  padding: 0;
  font-family: 'Proto', sans-serif;
  font-weight: 300;
  font-size: 100px;
  line-height: 100px;
  letter-spacing: -2px;
  text-align: center;
  color: #000;
}

.page-not-found__subtitle {
  margin: 0;
  margin-bottom: 50px;
  padding: 0;
  font-family: 'Proto', sans-serif;
  font-weight: 300;
  font-size: 23px;
  line-height: 30px;
  text-align: center;
  color: #000;
}

.page-not-found__subtitle:first-of-type {
  margin-bottom: 0;
}

@media (max-width: 1024px) {
  .page-not-found {
    padding-left: 4.6%;
    padding-right: 4.6%;
  }

  .page-not-found__title {
    font-size: 60px;
    line-height: 60px;
  }

  .page-not-found__subtitle {
    font-size: 19px;
    line-height: 23px;
  }
}

@media (max-width: 660px) {
  .page-not-found {
    margin-top: 50px;
    padding-left: 15px;
    padding-right: 15px;
  }

  .page-not-found__img {
    max-width: 100%;
    margin-bottom: 20px;
  }

  .page-not-found__title {
    margin-bottom: 10px;
  }

  .page-not-found__subtitle {
    margin-bottom: 40px;
  }
}
